/*

Exit Transition
showFront = true -> false
front -> back

Enter Transition
showFront = false -> true
back -> front

*/

.flip-exit {
	transform: rotateY(0deg);
}

.flip-exit-active {
	transform: rotateY(180deg);
	transition: transform 500ms;
	transition-timing-function: cubic-bezier(0.57, 0.21, 0.69, 1.25);
}

.flip-exit-done {
	transform: rotateY(180deg);
}

.flip-enter {
	transform: rotateY(180deg);
}

.flip-enter-active {
	transform: rotateY(0deg);
	transition: transform 300ms;
	transition-timing-function: cubic-bezier(0.57, 0.21, 0.69, 1.25);
}

.flip-enter-done {
	transform: rotateY(0deg);
}
